.main {
  background-color: #1d1d1d;

  &__content {
    display: grid;
    row-gap: 92px;
    padding-block: 92px;

    @include onTablet {
      padding-block: 120px;
      row-gap: 120px;
    }

    @include onDesktop {
      padding-block: 164px;
      row-gap: 164px;
    }
  }
}
